body {
  font-family: "Roboto";
}

.container {
  width: 1440px;
  padding-top: 96px;
}

.compotent {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  gap: 95px;
}
.compotent .content {
  width: 380px;
  color: #121212;
}
.compotent .content h2 {
  font-size: 42px;
  line-height: 50.4px;
  margin: 0 0 24px 0;
}
.compotent .content p {
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  margin: 0;
}
.compotent .content button {
  padding: 16px 40px;
  border-radius: 24px;
  font-size: 16px;
  line-height: 16px;
  background-color: #E4C1AE;
  color: white;
  font-weight: 700;
  border: none;
  margin-top: 32px;
  transition: background-color 200ms;
}
.compotent .content button:hover {
  background-color: #deb29b;
}
.compotent .content button:focus {
  background-color: #ead0c1;
}

.img-hero {
  width: 505px;
}

.img-hero img {
  width: 505px;
  height: 330px;
}